.footer {
  background-color: #dde1e7; //예외 컬러
  .inner {
    position: relative;
    max-width: 140rem;
    height: 100%;
    margin: 0 auto;
    padding: 1.6rem 4rem;
  }
  &__list {
    @include flex;
    gap: 0 1.4rem;
  }
  &__item {
    .link {
      position: relative;
      font-size: 1.3rem;
      font-weight: 700;
      color: $color_gray2;
      &::after {
        content: "";
        display: inline-block;
        margin-left: 1.4rem;
        width: 1px;
        height: 8px;
        vertical-align: middle;
        background-color: $color_gray4;
      }
    }
    &:first-child .link::before {
      content: "N";
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      z-index: 10;
      right: 0.4rem;
      top: -0.4rem;
      padding: 0.1rem;
      width: 1.2rem;
      height: 1.2rem;
      background-color: #ea4335;
      border-radius: 50%;
      color: $color_white;
      font-size: 0.8rem;
      font-weight: 500;
    }
    &:last-child .link::after {
      content: none;
    }
  }
  &__copy {
    display: block;
    margin-top: 0.4rem;
    @include txt_body4;
    color: $color_gray2;
  }

  // media
  @media screen and (max-width: 768px) {
    .inner {
      padding: 1.6rem 2rem;
    }
    &__list {
      gap: 0.6rem;
    }
    &__item {
      &:first-child .link::before {
        right: -0.1rem;
        top: -0.4rem;
        width: 1rem;
        height: 1rem;
      }
    }
    .link {
      @include txt_body4;
      font-weight: 600;
      &::after {
        margin-left: 0.6rem;
      }
    }
  }
}
